import React, { createContext, useCallback, useContext, useState } from "react";

export interface IUserData {
    email: string;
    name: string;
    lastName: string;
}

interface IUserContexData {
    user: IUserData;
    setUser(use: IUserData): void;
    setUserIdToken(idToken: string): void
    userIdToken: string
}
const UserDataContext = createContext<IUserContexData>({} as IUserContexData);

const UserDataProvider: React.FC = ({ children }) => {
    const [userData, setUserData] = useState<IUserData>({
        email: "",
        name: "",
        lastName: ""
    });
    const [userIdToken, setIdToken] = useState("")

    const setUser = useCallback((user: IUserData) => {
        setUserData(user);
    }, []);

    const setUserIdToken = useCallback((idToken: string) => {
        setIdToken(idToken)
    }, [])

    return (
        <UserDataContext.Provider
            value={{
                setUser,
                user: userData,
                setUserIdToken,
                userIdToken
            }}
        >
            {children}
        </UserDataContext.Provider>
    );
};

function useUserData(): IUserContexData {
    const context = useContext(UserDataContext);

    if (!context) {
        throw new Error("useUserData must be used within an UserDataProvider");
    }

    return context;
}

export { UserDataProvider, useUserData };
